.castContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding: 15px;
}
.actorImg {
  overflow: hidden;
}
.actorImg > img {
  max-width: 250px;
  min-height: 375px;
  object-fit: cover;
}
.actorItem {
  display: flex;
  flex-direction: column;
  height: 450px;
  overflow: hidden;
}
.ActorInfo {
  flex-grow: 1;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
}
.actorName {
  font-size: 18px;
  padding: 4px;
}
.actorChar {
  font-size: 14px;
  color: #555555;
  padding: 4px;
  word-wrap: break-word;
}
.noCast {
  font-size: 32px;
  font-weight: 600;
}
